body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* App Layout*/
.main {
  text-align: center;
}

.header {
  background: gray;
  height: 50px;
}

.title {
  font-size: 1.5em;
}

/* sidebar from react-burger-menu */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 10px;
}

.bm-burger-bars {
  background: #373a47;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #bdc3c7;
}

.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

.bm-morph-shape {
  fill: #373a47;
}

.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

.bm-item {
  display: inline-block;
}

.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
  position: relative !important;
}

.bm-menu-wrap {
  top: 50px;
}

/* Places list */
#places-list, #search-input {
  padding: 5px;
  width: 100%;
}

#places-list li {
  list-style: none;
  padding: 10px;
  margin: 15px 0px;
  border: 1px solid grey;
  box-shadow: 0px 1px 1px grey;
  background: white;
  cursor: pointer;
}

#places-list li:hover {
  background: lightgrey;
}

/* Map CSS */
#containerElement {
  top: 50px;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
}

/* Modal window - when data fails to laod */
.content {
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  marginRight: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.ReactModal__Overlay  {
  height: 160px;
  background: lightgrey;
  top: 100px;
}
